import "./styles.css";
import MediaQuery from "react-responsive";
import PC from "./pc";
import PHONE from "./phon";
import menu from "./menu";
import * as React from "react";

import { Button } from "@mui/material";

import Card from "@mui/material/Card";
import CardContent from "@mui/material/CardContent";
import CardMedia from "@mui/material/CardMedia";
import Typography from "@mui/material/Typography";
import { CardActionArea } from "@mui/material";
function menus(index) {
  const items = [];
  const keys = Object.keys(menu[index]);
  const values = Object.values(menu[index]);
  for (let i = 0; i < values.leagth; i++) {
    items.push(
      <li>
        {keys[i]}:{values[i]}
      </li>
    );
  }
  return <ul>{items}</ul>;
}
function all_menus() {
  const items = [];
  for (let i = 0; i < menu.leagth; i++) {
    items.push(menu(i));
  }
  return <>{items}</>;
}
export default function App() {
  return (
    <div className="App">
      <MediaQuery query="(max-width:650px)">
        <PC />
      </MediaQuery>
      <MediaQuery query="(min-width:650px)">
        <PHONE />
      </MediaQuery>
    </div>
  );
}
